.shooting-stars-container {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: var(--primary-color);
    overflow: hidden;
}

@media (min-width: 320px) and (max-width: 480px) {
    .shooting-stars-container{
        height: 100dvh;
    }
}

@media (min-width: 481px) and (max-width: 768px){
    .shooting-stars-container{
        height: 100dvh;
    }
}